import { Component, OnInit } from '@angular/core';
import {CardModalService} from '../card-modal/card-modal.service';
import { CardSearchService } from '../services/card-search.service';
import { CardModalComponent } from '../card-modal/card-modal.component';

import {MdDialog, MdDialogRef} from '@angular/material';

import { SearchBarComponent } from '../search-bar/search-bar.component';

//import { SearchFilterComponent } from '../search-filter/search-filter.component';
import {SearchFilterDialogComponent} from '../search-filter-dialog/search-filter-dialog.component';

import appStore from '../app.store';

import 'hammerjs';

@Component({
  selector: 'app-deck-builder',
  templateUrl: './deck-builder.component.html',
  styleUrls: ['./deck-builder.component.scss'],
  providers: [CardSearchService, CardModalComponent]
})

export class DeckBuilderComponent implements OnInit {
  modalOpen: Boolean = false;
  allCards = [];
  colours = ['Black', 'Blue', 'Green', 'Red', 'White'];
  currentColour = 'Black';
  selectedOption;
  store;

  constructor(
    private cardSearchService: CardSearchService,
    private cardModalService: CardModalService,
    public dialog: MdDialog
    ) {
    this.store = appStore.deckBuilder;
  }


  ngOnInit() {
    //this.updateCards();
  }

  openSearchDialog() {
    let dialogRef = this.dialog.open(SearchFilterDialogComponent);
    dialogRef.afterClosed().subscribe(result => {
      this.selectedOption = result;
    });
  }

  updateCards () {
    /*
    this.cardSearchService.search({
      colours: [this.currentColour]
    }).subscribe((data)=>{
      console.log('got', data);

      //this.allCards = data.cards;
      let previousResults = this.store.searchResults;
      this.store.searchResults.splice(0, previousResults.length - 1);
      data.cards.forEach((obj) => {
        this.store.searchResults.push(obj);
      });
    });
    */

  }

  inspectCard (card) {
    this.cardModalService.show(card);
    this.modalOpen = true;
    this.cardModalService.activate();
  }

  
  colourChange ($event) {
    console.log('colour change ', $event);
    this.currentColour = $event.target.value;
    this.updateCards();
  }

  modalClosedHandler () {
    this.modalOpen = false;
  }
}

