import { Injectable } from '@angular/core';

@Injectable()
export class CardModalService {

  public card: any;
  public active: boolean;

  constructor() {
    this.card = null;
  }

  show (card) {
    console.log('Will show ', card, this);
    this.card = card;
    console.log('set it ? ', card, this);
  }
  
  activate () {
    this.active = true;
  }
  deactivate () {
    this.active = false;
  }

}



