import { Component, OnInit, Output, EventEmitter} from '@angular/core';
import {CardModalService} from './card-modal.service';

interface DisplayState {
    state: string;
}

@Component({
  selector: 'app-card-modal',
  templateUrl: './card-modal.component.html',
  styleUrls: ['./card-modal.component.scss']
})
export class CardModalComponent implements OnInit {

  @Output() open: EventEmitter<any> = new EventEmitter();
  @Output() close = new EventEmitter();

  constructor (private cardModalService: CardModalService) {

  }

  ngOnInit() {
  }
  cardClick (event) {
    event.preventDefault();
    event.stopPropagation();
  }

  swipe (event, card) {
    console.log('swipe event ', card, event);
  }
  
  drag (event) {
    event.preventDefault();
  }
  
  closeModal () {
    console.log('service close');

    this.close.emit({
      card:this.cardModalService.card
    });

    this.cardModalService.deactivate();
  }

}



