import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';
import { RouterModule, Routes }   from '@angular/router';

import { MaterialModule } from '@angular/material';

import { Ng2MobxModule } from 'ng2-mobx';

import { AppComponent } from './app.component';
import { CardModalComponent } from './card-modal/card-modal.component';
import { CardModalService } from './card-modal/card-modal.service';
import { DashboardComponent } from './dashboard/dashboard.component';
import { DeckBuilderComponent } from './deck-builder/deck-builder.component';
import { SearchBarComponent } from './search-bar/search-bar.component';
import { SearchFilterDialogComponent } from './search-filter-dialog/search-filter-dialog.component';
import { SiteHeaderComponent } from './site-header/site-header.component';

const routes: Routes = [
  { path: '', redirectTo: '/dashboard', pathMatch: 'full' },
  { path: 'dashboard',  component: DashboardComponent },
  { path: 'deck-builder', 
    component: DeckBuilderComponent
  }
];

@NgModule({
  declarations: [
    AppComponent,
    CardModalComponent,
    DashboardComponent,
    DeckBuilderComponent,
    SearchBarComponent,
    SearchFilterDialogComponent,
    SiteHeaderComponent
  ],
  imports: [
    BrowserModule,
    FormsModule,
    HttpModule,
    MaterialModule.forRoot(),
    Ng2MobxModule,
    RouterModule.forRoot(routes)
  ],
  providers: [CardModalService],
  entryComponents: [SearchFilterDialogComponent],
  bootstrap: [AppComponent]
})
export class AppModule { }



