import { Component, OnInit } from '@angular/core';
import { CardSearchService } from '../services/card-search.service';
import {FormControl, FormGroup, FormArray, Validators, FormBuilder} from '@angular/forms';

import appStore from '../app.store';

@Component({
  selector: 'app-search-bar',
  templateUrl: './search-bar.component.html',
  styleUrls: ['./search-bar.component.scss'],
  providers: [CardSearchService]
})
export class SearchBarComponent implements OnInit {
  store;
  coloursCheckBoxArray:FormArray;
  model = {
    colours: []
  };

  constructor(
    private cardSearchService: CardSearchService
  ) {
    this.store = appStore.deckBuilder;
  }

  ngOnInit() {
    this.coloursCheckBoxArray = new FormArray([]);
    for( let c in this.colours ) {
      console.log('let colour', this.colours[c]);
      this.coloursCheckBoxArray.push( new FormControl(this.colours[c].value));
    }
  }

  colours =[{
    name: 'Colourless',
    value: 'colourless',
    img: 'assets/images/colourless.png',
    checked: false
  }, {
    name: 'Black',
    value: 'black',
    img: 'assets/images/black.png',
    checked: false
  }, {
    name: 'Blue',
    value: 'blue',
    img: 'assets/images/blue.png',
    checked: false
  }, {
    name: 'Green',
    value: 'green',
    img: 'assets/images/green.png',
    checked: false
  }, {
    name: 'Red',
    value: 'red',
    img: 'assets/images/red.png',
    checked: false
  }, {
    name: 'White',
    value: 'white',
    img: 'assets/images/white.png',
    checked: false
  }];


  updateCards () {
    console.log('in search BAR version ', this.model);
    this.cardSearchService.search(this.model);
  }

  manaClicked (value, event) {
    console.log('mana clicked', value, event);
    console.log('colours', this.colours);
    console.log('appStore in search-filter', appStore);

    this.model.colours = this.colours
      .filter((obj)=> obj.checked === true )
      .map((obj)=> obj.value);

    console.log('modelColours', this.model.colours);
  }

  setFilter() {
    console.log('set filter', this);
    this.updateCards();
  }
}



